.CalculatorMainDiv {
    background: #eee9e9;
    height: 550px;
    width: 100vw;
    padding-top: 1px;
    margin-top: -1px;

}



table {
    border: 1px solid #1a2a33;
    margin-left: auto;
    margin-right: auto;
    margin-top: 70px;

}

input[type="button"] {
    width: 100%;
    padding: 20px 40px;
    background-color: #1a2a33;
    color: white;
    font-size: 24px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
}

input[type="text"] {
    border: 2px solid black;
    border-radius: 5px;
    padding: 20px 30px;
    font-size: 24px;
    font-weight: bold;

}


@media (max-width:527px) {

    input[type="button"] {
        font-size: 20px;

    }

    input[type="text"] {
        font-size: 20px;


    }
}